import React from "react";

import EventBusyIcon from "@mui/icons-material/EventBusy";

import ErrorAddresse from "./Error/ErrorAddresse";
import Addresses from "./Addresses/Addresses";
import WeekCalendar from "../../components/WeekCalendar/WeekCalendar";
import { Typography } from "@mui/material";
import DroppableArea from "./DragAndDrop/DroppableArea";

function LeftSide({
	error,
	currentDate,
	setCurrentDate,
	leftItems,
	handleDrop,
	team,
	setTeam,
}) {
	return (
		<div className="panel scheduling__split-panel panel2">
			<WeekCalendar
				team={team}
				setTeam={setTeam}
				currentDate={currentDate}
				setCurrentDate={setCurrentDate}
			/>
			{error ? (
				<ErrorAddresse err={error} />
			) : leftItems.length ? (
				<DroppableArea onItemDropped={handleDrop} target="left">
					<Addresses items={leftItems} handleDrop={handleDrop} />
				</DroppableArea>
			) : (
				<Typography className="addresses__no-addresse" variant="h3">
					Keine Adressen gefunden &nbsp;
					<EventBusyIcon />
				</Typography>
			)}
		</div>
	);
}

export default LeftSide;
