import { useEffect, useState } from "react";

import Split from "react-split";

import ErrorAddresse from "./Error/ErrorAddresse.jsx";

import LeftSide from "./LeftSide.jsx";
import RightSide from "./RightSide.jsx";

import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";

import useGetJSON from "../../api/useGetJSON.js";

import getFBWeekNumber from "../../utils/getFBWeekNumber.js";
import sortDataById from "../../utils/sortDataById.js";
import getDataFromLocalStorage from "../../utils/getLS.js";

const Scheduling = () => {
	const [sizes, setSizes] = useState(() => {
		const savedSizes = JSON.parse(localStorage.getItem("splitPaneSizes"));
		return savedSizes || [50, 50];
	});
	const [weekday, setWeekday] = useState("Monday");
	const [team, setTeam] = useState("Team1");
	const [currentDate, setCurrentDate] = useState(new Date());
	const [weekFB, setWeekFB] = useState(getFBWeekNumber());

	const [leftItems, setLeftItems] = useState([]);
	const [rightItems, setRightItems] = useState([]);

	const { data, loading, error, setError } = useGetJSON("dbData", "/db.json");

	useEffect(() => {
		setWeekFB(getFBWeekNumber(currentDate));
	}, [currentDate]);

	useEffect(() => {
		localStorage.setItem("splitPaneSizes", JSON.stringify(sizes));
	}, [sizes]);

	useEffect(() => {
		if (!loading) {
			try {
				setLeftItems(sortDataById(data["weekly_unassigments"][weekFB]));
				setRightItems(
					sortDataById(data["weekly_assignments"][weekFB][weekday][team])
				);
			} catch (error) {
				setError("Etwas ist schief gelaufen");
			}
		}
	}, [loading, weekFB]);

	useEffect(() => {
		if (!loading) {
			try {
				if (team === "Alle Touren") {
					// При нажатии на показать все туры
					const dbData = getDataFromLocalStorage("dbData");

					// Получаем массивы заданий для указанной недели
					const arr = dbData.weekly_assignments[weekFB][weekday];
					const combinedTasks = Object.values(arr).flat();

					setRightItems(sortDataById(combinedTasks));
				} else {
					setRightItems(
						sortDataById(data["weekly_assignments"][weekFB][weekday][team])
					);
				}
			} catch (error) {
				setError("Etwas ist schief gelaufen");
			}
		}
	}, [weekday, team]);

	const handleDragEnd = newSizes => {
		setSizes(newSizes);
	};

	// Добавить адрес в назначеный
	const addToAssigment = (assignmentId, week, day, team) => {
		// Получаем данные из localStorage
		const dbData = getDataFromLocalStorage("dbData");

		// Получаем массивы заданий для указанной недели
		const weeklyAssignments = dbData.weekly_assignments[week];
		const weeklyUnassigments = dbData.weekly_unassigments[week];

		// Находим индекс объекта в массиве заданий по id
		const index = weeklyUnassigments.findIndex(
			item => item.id === assignmentId
		);

		if (index !== -1) {
			// Удаляем объект из массива заданий
			const removedAssignment = weeklyUnassigments.splice(index, 1)[0];

			// Проверяем, существует ли день
			if (!weeklyAssignments[day]) {
				// Если для данного дня нет данных, создаем новый объект
				weeklyAssignments[day] = {};
			}

			// Проверяем, существует ли массив для команды в данном дне
			if (!weeklyAssignments[day][team]) {
				// Если для данной команды нет данных, создаем новый массив
				weeklyAssignments[day][team] = [];
			}

			// Добавляем объект в массив для команды в нужный день
			weeklyAssignments[day][team].push({ ...removedAssignment });

			console.log(weeklyAssignments[day]);
			// Обновляем данные в dbData
			localStorage.setItem("dbData", JSON.stringify(dbData));

			// Возвращаем обновленные данные для отображения
			return { weeklyAssignments, weeklyUnassigments };
		}

		return null; // Возвращаем null, если объект не найден
	};
	// Убрать адрес из назначеного
	const removeFromAssigment = (assignmentId, week, day, team) => {
		// Получаем данные из localStorage
		const dbData = getDataFromLocalStorage("dbData");

		// Получаем массивы заданий для указанной недели
		const weeklyAssignments = dbData.weekly_assignments[week];
		const weeklyUnassigments = dbData.weekly_unassigments[week];

		// Проверяем, существует ли день и команда
		if (weeklyAssignments[day] && weeklyAssignments[day][team]) {
			// Находим индекс объекта в массиве заданий дня недели по id
			const index = weeklyAssignments[day][team].findIndex(
				item => item.id === assignmentId
			);

			if (index !== -1) {
				// Удаляем объект из массива заданий команды дня недели
				const removedAssignment = weeklyAssignments[day][team].splice(
					index,
					1
				)[0];

				// Добавляем объект обратно в список невыполненных заданий
				weeklyUnassigments.push({ ...removedAssignment });

				// Обновляем данные в dbData
				localStorage.setItem("dbData", JSON.stringify(dbData));

				// Возвращаем обновленные данные для отображения
				return { weeklyAssignments, weeklyUnassigments };
			}
		}

		return null; // Возвращаем null, если объект не найден
	};

	// Функция которая отвечает за пермещение элменетов с обоих сторон и замену состояния.
	const handleDrop = (item, target) => {
		let sourceItems, setSourceItems, targetItems, setTargetItems;

		if (team === "Alle Touren") return false;

		if (target === "left") {
			sourceItems = rightItems;
			setSourceItems = setRightItems;
			targetItems = leftItems;
			setTargetItems = setLeftItems;
		} else {
			sourceItems = leftItems;
			setSourceItems = setLeftItems;
			targetItems = rightItems;
			setTargetItems = setRightItems;
		}

		const droppedItem = sourceItems.find(i => i.id === item.id);
		if (!droppedItem) return; // Check if dropped item exists to prevent undefined error

		const newSourceItems = sourceItems.filter(i => i.id !== item.id);
		const newTargetItems = [...targetItems, droppedItem];

		const sortedSourcesItems = sortDataById(newSourceItems);
		const sortedTargetItems = sortDataById(newTargetItems);

		setSourceItems(sortedSourcesItems);
		setTargetItems(sortedTargetItems);

		// Вызов функции отправки данных на сервер
		sendDataToServer(droppedItem, target);
	};

	// Функция для отправки данных на сервер
	const sendDataToServer = (item, target) => {
		if (target === "right") {
			addToAssigment(item.id, weekFB, weekday, team);
		}
		if (target === "left") {
			removeFromAssigment(item.id, weekFB, weekday, team);
		}
	};

	return (
		<section className="scheduling">
			<div className="scheduling__content">
				<div className="split-container scheduling__split-container">
					<Split
						sizes={sizes} // Размеры панелей в процентах (по умолчанию 50% каждая)
						minSize={500} // Минимальный размер каждой панели в пикселях
						expandToMin={false}
						gutterSize={6} // Размер линии перетаскивания
						gutterAlign="center"
						snapOffset={30}
						dragInterval={15}
						direction="horizontal" // Горизонтальное деление
						cursor="col-resize"
						className="split"
						onDragEnd={handleDragEnd}
					>
						<DndProvider backend={HTML5Backend}>
							<LeftSide
								error={error}
								loading={loading}
								currentDate={currentDate}
								setCurrentDate={setCurrentDate}
								leftItems={leftItems}
								handleDrop={handleDrop}
								team={team}
								setTeam={setTeam}
							/>

							<div className="panel scheduling__split-panel scheduling__weekdays">
								{error ? (
									<ErrorAddresse err={error} />
								) : (
									<RightSide
										weekday={weekday}
										weekFB={weekFB}
										setWeekday={setWeekday}
										rightItems={rightItems}
										handleDrop={handleDrop}
										team={team}
										setTeam={setTeam}
									/>
								)}
							</div>
						</DndProvider>
					</Split>
				</div>
			</div>
		</section>
	);
};

export default Scheduling;
